<template>
  <div class="animated slideInLeft" style="animation-duration:100ms">
  	<b-card id="studentform">
  			<b-tabs>
  				<b-tab id="studenttab" active :title="$ml.get('student')">
				  	<b-form v-on:submit.prevent="editStudent">
				  		<b-row class="float-right">
				  			<div id="edit-icon-container">
				  				<span id="edit-icon"  @click="onEditClick">
				  					<i class="icon-pencil"></i>
				  				</span>

				  				<span v-if="show_updload_icon"  id="edit-icon-cu" @click="onPicSubmit">
				  					<i class="cui-cloud-upload"></i>
				  				</span>
				  				<img class="avatar1" :src="profile_pic">
				  			</div>

				  		</b-row>
				  		<b-row>
				  			<b-col>
				  				<label v-text="$ml.get('username')"></label>
				  				<b-form-input type="text" v-model="student.user_name" :placeholder="$ml.get('username')"></b-form-input>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('password')"></label>
				  				<b-form-input type="password" v-model="student.password" :placeholder="$ml.get('password')"></b-form-input>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('programme')"></label>
				  				<b-form-select v-model="student.programme" :options="programmes" value-field="_id" text-field="name" disabled-field="disabled" :placeholder="$ml.get('programme')"></b-form-select>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('academicterm')"></label>
				  				<b-form-select v-model="student.academic_term" :options="academic_terms" value-field="_id" text-field="name" disabled-field="disabled" :placeholder="$ml.get('academicterm')"></b-form-select>
				  			</b-col>
				  		</b-row>
				  		<b-row>
				  			<b-col>
				  				<label v-text="$ml.get('name')"></label>
				  				<b-form-input type="text" v-model="student.personal_details.name" :placeholder="$ml.get('name')"></b-form-input>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('email')"></label>
				  				<b-input-group prepend="@">
					  				<b-form-input type="email" v-model="student.personal_details.email" :placeholder="$ml.get('email')"></b-form-input>
				  				</b-input-group>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('phone')"></label>
				  				<b-form-input type="number" v-model="student.personal_details.phone" :state="validatePhone" :placeholder="$ml.get('phone')"></b-form-input>
				  				<b-form-invalid-feedback :state="validatePhone" v-text="$ml.get('errorphone')">
							    </b-form-invalid-feedback>
				  			</b-col>
				  		</b-row>
				  		<b-row>
				  			<!-- <b-col>
				  				<label v-text="$ml.get('fathersname')"></label>
				  				<b-form-input type="text" v-model="student.personal_details.father_name" :placeholder="$ml.get('fathersname')"></b-form-input>
				  			</b-col> -->
				  			<b-col>
				  					<b-row>
				  						<label v-text="$ml.get('fathersname')"></label>
				  				        <b-form-input type="text" v-model="student.personal_details.father_details.fname" :placeholder="$ml.get('fathersname')"></b-form-input>
				  					</b-row>
				  					<b-row>
				  				         <label v-text="$ml.get('fathercontact')"></label>
				  				           <b-form-input type="number" v-model="student.personal_details.father_details.contact"  :state="validateFatherPhone" :placeholder="$ml.get('fathercontact')"></b-form-input>
				  				           <b-form-invalid-feedback :state="validateFatherPhone" v-text="$ml.get('errorphone')"></b-form-invalid-feedback>
				  					</b-row>
				  			</b-col>

				  			<b-col>
				  					<b-row>
				  						<b-col>
				  							<label v-text="$ml.get('mothersname')"></label>
				  				        <b-form-input type="text" v-model="student.personal_details.mother_details.mname" :placeholder="$ml.get('mothersname')"></b-form-input>
				  						</b-col>
				  					</b-row>
				  					<b-row>
				  						<b-col>
				  							<label v-text="$ml.get('mothercontact')"></label>
				  				        <b-form-input type="number" v-model="student.personal_details.mother_details.contact" :state="validateMotherPhone" :placeholder="$ml.get('mothercontact')"></b-form-input>
				  				        <b-form-invalid-feedback :state="validateMotherPhone" v-text="$ml.get('errorphone')"></b-form-invalid-feedback>
				  						</b-col>
				  					</b-row>
				  			</b-col>

				  			<b-col>
				  				<label v-if="edit_pic" v-text="$ml.get('profilepic')"></label>
				  				<b-form-file id="studentprofilepic" v-if="edit_pic" @input="onChange"  v-model="student.personal_details.profile_pic" :placeholder="$ml.get('profilepic')"></b-form-file>
				  			</b-col>
				  		</b-row>
				  		<b-row>
				  			<b-col>
				  				<label v-text="$ml.get('currentaddress')"></label>
				  				<b-form-textarea v-model="student.personal_details.current_address" :placeholder="$ml.get('currentaddress')"></b-form-textarea>
				  			</b-col>
				  			<b-col>

				  				<label v-text="$ml.get('corresaddress')"></label>
				  				<b-form-textarea v-model="student.personal_details.corresspondence_address" :placeholder="$ml.get('corresaddress')"></b-form-textarea>
				  				<b-form-checkbox @change="copyAddr" switch size="sm" v-model="copyAddress" value="true" unchecked-value="false">Same as Current Address</b-form-checkbox>
				  			</b-col>
				  		</b-row>
				  		<br>
				  		<div class="footer">
				  			<b-button pill variant="primary" type="submit" v-text="$ml.get('submit')"></b-button>
				  		</div>
				  	</b-form>
				</b-tab>
		  	</b-tabs>
  	</b-card>
  </div>
</template>
	
<script>

	//Imports
	import {acadUrl} from '@/apiUrl'
	import apiUrl from '@/apiUrl'
	import axios from 'axios'
	import Vue from 'vue'
	import VueNotifications from 'vue-notifications'
	import miniToastr from 'mini-toastr' 

	//Declarations
    var api = axios.create({
	  withCredentials :true
	})
	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	//Function Calls
	miniToastr.init({types: toastTypes})
	Vue.use(VueNotifications, options)

    //Vue Handler
	export default {
		name:"StudentDetails",
		components:{

		},
		data : function () {
			return {
				//form data
				student : {
					personal_details: {
						phone:'',
					    mother_contact:'',
					    father_contact:''
					}
				},
				edit_pic:false,
				copyAddress:false,
				profile_pic:"",
				show_updload_icon: false,
				//Programme List
				programmes : [{name:"No Programmes",_id:"0",disabled:true}],
				//Academic-Term List
				academic_terms : [{name:"No Academic-Terms",_id:"0",disabled:true}]
			}
		},
		methods:{
			// Send Edit Request
			editStudent(args) {
				console.log(this.student);
				api.put(`${acadUrl}stud/student/update/one/${this.$route.params.studId}`,this.student).then((student) => {
					this.$router.push({name: 'Student List'});
				})
			},
			//Copy address
			copyAddr(args) {
				if(args=="true") {
					this.student.personal_details.corresspondence_address = this.student.personal_details.current_address
				}
			},
			// edit event
	        onEditClick(){
		     this.edit_pic = true;
	      },
	      onChange(){
	      	 if(this.student.personal_details.profile_pic.name){
	      		this.show_updload_icon = true;
	      	}
	      },
	      onPicSubmit(){
	      	console.log("pic submit");
	      	var pictureData = new FormData();
	      	pictureData.append('profile_pic',this.student.personal_details.profile_pic);
	      	if(this.student.personal_details.profile_pic.name){
	      		// call api here
	      		api.put(`${acadUrl}/stud/student/update/profile/pic/${this.$route.params.studId}`, pictureData, {headers:{'Content-Type':'multipart/form-data'}})
	      		  .then(res => {
	      		  	console.log(res);
	      		  	alert('res data');
	      		  })
	      		  .catch(error => {
	      		  	console.log(error);
	      		  	alert('error');
	      		  });
	      	}
	      	
	      }
		},
		async mounted(){
			var apilink = apiUrl.split('/api')
			console.log(this.$route.params)
			await api.get(`${acadUrl}stud/student/view/one/${this.$route.params.studId}`).then((student) => {
				this.student = student.data
			})
			this.profile_pic = `${apilink[0]}/${this.student.personal_details.profile_pic}`
		},
		computed: {
			// Validate Fields
			validatePhone() {
				return this.student.personal_details.phone.length==10 && this.student.personal_details.phone>0
			},
			validateMotherPhone() {
				return this.student.personal_details.mother_details.contact.length==10 && this.student.personal_details.mother_details.contact>0
			},
			validateFatherPhone() {
				return this.student.personal_details.father_details.contact.length==10 && this.student.personal_details.father_details.contact>0
			}
		}
	};
</script>

